import React from 'react'
import style from './TopProduct.module.scss'
import 'aos/dist/aos.css'

type PropsType = {
    header: string
    text: string
    Icon: React.ComponentType
}

export const TopProduct: React.FC<PropsType> = React.memo(({header, text, Icon}) => {

    return (
        <div className={style.topProduct}>
            <div className={style.icon} data-aos='flip-left' data-aos-duration={600} >
                <Icon/>
            </div>
            <div className={style.header} data-aos={'fade-up'} data-aos-duration={600} data-aos-delay={80}>
               {header}
            </div>
            <p className={style.text} data-aos={'fade-up'} data-aos-duration={1000} data-aos-delay={150}>
              {text}
            </p>
        </div>
    )
})