import React from 'react'
import style from './AboutUsItems.module.scss';
import { NavLink } from 'react-router-dom';
import 'aos/dist/aos.css'


type PropsType = {
    Icon: React.ComponentType
    header: string
    text: string
    link: string;
}

export const AboutUsItem: React.FC<PropsType> = React.memo(({Icon, header, text, link}) => {

    return (
        <div className={style.aboutUsItem}>
            <div className={style.icon} data-aos={'flip-up'} data-aos-duration={600}>
                <Icon/>
            </div>
            <div className={style.header} data-aos={'zoom-in-up'} data-aos-duration={600} data-aos-delay={100}>
                {header}
            </div>
            <p className={style.text}  data-aos={'zoom-in-up'} data-aos-duration={600} data-aos-delay={200}>
                {text}
            </p>
            <div className={style.link}>
               <NavLink to={''}>-{link}-</NavLink>
            </div>
        </div>
    )
})